body{font-family:'Montserrat', sans-serif !important;font-size:large !important}.quote{fill:#DF4459;width:60px;height:60px;position:absolute;left:25px;top:25px;z-index:15;opacity:0.8;transform:rotate(180deg);filter:progid:DXImageTransform.Microsoft.Matrix(M11=-1, M12=0, M21=0, M22=-1, DX=0, DY=0, SizingMethod='auto expand')}.recomText{z-index:30;margin:15px 0 0 35px}.btn-outline-primary{color:#DF4459 !important;border-color:#DF4459 !important}.btn-outline-primary:hover{background-color:#DF4459 !important;color:white !important;border-color:white !important}.btn-outline-primary:focus{box-shadow:0 0 0.2rem #DF4459 !important}.btn{border-radius:20px !important;padding:5px 16px !important;margin:5px 5px 0 0}.btn-small{border-radius:15px !important;padding:2px 6px !important;font-size:0.9rem !important}.section{padding:50px 0 !important}.topPad{padding-top:80px !important}.pink{background-color:#f1e3e5}.center-row{justify-content:center}.contentCard{margin:0 150px;padding:20px 50px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.05),0 6px 20px 8px rgba(0,0,0,0.05);border:none}.certi-card{width:14rem;margin:5px}.certi-card .card-body{padding:0.9rem !important}.certi-card .card-title{font-size:1rem !important;margin-bottom:0.3rem !important;font-weight:bold !important}.certi-card .card-text{font-size:0.9rem !important;margin-bottom:0.5rem !important}.certi-card .card-img{opacity:0.8;-webkit-filter:grayscale(10%);filter:grayscale(10%)}.rightText{text-align:right}.pinkBold{font-weight:bold;color:#d37f8a}.darkBold{font-weight:bold;color:#DF4459}.email{color:black;text-decoration:underline}.email:hover,.email:focus{color:black;text-decoration:underline}.publish{color:#DF4459;text-decoration:underline}.publish:hover,.publish:focus{color:#DF4459;text-decoration:underline}.subTitle{text-align:center;color:gray;margin:30px 0}.footerText{text-align:center;color:gray;margin:30px 0}.project-title{color:#DF4459;padding:5px 0;font-weight:700}.project-subtitle{color:#d37f8a;font-style:italic}.section-title{display:flex;justify-content:center;align-items:center;padding-bottom:20px}.section-title .section-title-text{margin-left:10px;font-weight:800}.section-title .dot{height:20px;width:20px;background-color:#DF4459;border-radius:50%}.scrollTop{position:fixed;bottom:20px;right:20px;z-index:1000;cursor:pointer;-webkit-animation:fadeIn 0.3s;animation:fadeIn 0.3s;transition:opacity 0.4s;opacity:0.5;fill:#d37f8a}.scrollTop:hover{opacity:1}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:0.5}}@keyframes fadeIn{0%{opacity:0}100%{opacity:0.5}}.pic{width:100%}

